.authorization {
  width: 390px;
  height: 500px;
  border-radius: 5px;
  background: #fff;
  position: fixed;
  top: 45%;
  left: 50%;
  margin-top: -130px;
  margin-left: -195px;
  display: none;
  opacity: 0;
  z-index: 10;
}
.authorization .typeOfAuthor {
  margin-bottom: 30px;
}
.authorization .typeOfAuthor span {
  display: inline-block;
  width: 50%;
  height: 65px;
  font: 1.125em/65px "PTSans-Bold";
  color: #00929e;
  text-align: center;
}
.authorization .typeOfAuthor span:hover {
  cursor: pointer;
}
.authorization .typeOfAuthor span:nth-child(2) {
  color: #98a2a4;
  background-color: #ebebeb;
}
.authorization .author {
  text-align: center;
}
.authorization .author p {
  position: relative;
  margin: 10px 0;
}
.authorization .author p .user,
.authorization .author p .shield {
  position: absolute;
  top: 15px;
  left: 75px;
  color: #00929e;
}
.authorization .author p input {
  width: 200px;
  height: 40px;
  padding-left: 50px;
  font: 1em/40px "PTSans-Regular";
  color: #828282;
  border: 1px solid #e6e6e6;
  border-radius: 5px;
  outline: none;
}
.authorization .author p .asterisk {
  position: absolute;
  top: 20px;
  right: 85px;
  font-size: .35em;
  color: #ff0000;
}
.authorization .author p .checkbox {
  display: none;
}
.authorization .author p .checkbox-custom {
  position: relative;
  width: 20px;
  height: 20px;
  border: 1px solid #e6e6e6;
  border-radius: 5px;
}
.authorization .author p .checkbox-custom,
.authorization .author p .label {
  display: inline-block;
  vertical-align: center;
}
.authorization .author p .checkbox:checked + .checkbox-custom:before {
  content: "";
  display: block;
  position: absolute;
  top: 2px;
  right: 2px;
  bottom: 2px;
  left: 2px;
  background-color: #00929e;
  border-radius: 5px;
}
.authorization .author button {
  margin: 30px 0;
  width: 250px;
  height: 40px;
  font: 1em/1.125em "PTSans-Regular";
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  background-color: #00929e;
  border: 1px solid #00929e;
  border-radius: 5px;
  outline: none;
}
.authorization .author button:hover {
  cursor: pointer;
}
.authorization .author a {
  display: block;
  font: 0.875em/1em "PTSans-Regular";
  color: #828282;
}
.authorization .socialAuthor {
  text-align: center;
}
.authorization .socialAuthor p {
  margin: 25px 0;
  font: 1em/1.125em "PTSans-Bold";
  color: #00929e;
}
.authorization .socialAuthor ul {
  display: flex;
  justify-content: center;
}
.authorization .socialAuthor ul li a {
  margin: 0 10px;
  display: inline-block;
  width: 140px;
  height: 40px;
  font: 1em/40px "PTSans-Regular";
  color: #fff;
  text-align: center;
  text-decoration: none;
  border-radius: 5px;
}
.authorization .socialAuthor ul li .facebook {
  background-color: #3b5998;
  border-bottom: 1px solid #293e6a;
}
.authorization .socialAuthor ul li .gPlus {
  background-color: #db4437;
  border-bottom: 1px solid #8a251d;
}
